.tiles_wrap {
  // padding: 10px 15px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
  flex-wrap: wrap;
  .tiles {
    max-width: 166px;
    width: 100%;
    transition: all 0.2s;
    padding: 10px 15px;
    border-radius: 10px;
    box-shadow: rgba(10, 53, 83, 0.25) 0px 4px 8px -2px,
      rgba(10, 53, 83, 0.08) 0px 0px 0px 1px;
    border: var(--border);
    cursor: pointer;
    &:hover {
      box-shadow: rgba(10, 53, 83, 0.45) 0px 25px 20px -20px;
    }
    .title {
      text-transform: capitalize;
      text-align: center;
    }
    img {
      max-width: 115px;
    }
    .text {
      font-size: 22px;
      text-align: right;
      margin-top: 20px;
      color: var(--primary-color);
    }
  }
  &.weather_tiles {
    text-align: center;
    .text {
      font-size: 15px;
    }
  }
}
